@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@100;400;700&display=swap);
body {
  margin: 0;
  font-family:'Roboto Mono', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --deep_purple: #00012b;
  --white: #ffffff;
  --light_purple: #71729a;
}

.App {
  color: black;
}
.home {
    color: var(--white);
    background-color: var(--deep_purple);
    background-image: url(/static/media/MyPost.456d2d2f.png);
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    min-height: 100vh;
    width: 100vw;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.home-title {
    font-size: 4em;
    letter-spacing: 2px;
    margin-bottom: 10px;
}

.home-desc {
    font-size: 1em;
    letter-spacing: 3px;
    margin-bottom: 30px;
}

.home-links {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    width: 30vw;
}

.home-button {
    color: white;
    width: 102px;
    letter-spacing: 2px;
    transition: all 0.5s ease;
    margin-bottom: 10px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
}

.home-button:hover {
    font-weight: bold;
}

@media only screen and (max-width: 530px) {
    .home-title {
        font-size: 2em;
    }

    .home-desc {
        font-size: 10px;
    }
}
.about {
    background-color: var(--white);
    color: var(--deep_purple);
    min-height: 100vh;
    width: 100vw;
    max-width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.cluster {
    border: 3px solid var(--deep_purple);
    border-radius: 5px;
    padding: 10px;
    height: 420px;
    width: 830px;
    display: flex;
    align-items: center;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: space-around;
}

.about-title {
    font-size: 5em;
    width: 46.9%;
    padding-right: 20px;
    height: 170px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.about-desc {
    width: 46.9%;
    padding: 10px;
    border: 1px solid var(--deep_purple);
    border-radius: 5px;
    height: 220px;
}

.skills {
    border: 1px solid var(--deep_purple);
    border-radius: 5px;
    padding: 10px;
    width: 46.9%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.skills > div > i {
    font-size: 50px;
}

.about-header {
    height: 30px;
    font-size: 2em;
    text-align: center;
    padding: 10px;
    width: 46.9%;
    text-decoration: underline;
    display: flex;
    justify-content: center;
    align-items: center;
}

.about-link {
    height: 40px;
    text-align: center;
    padding: 10px;
    width: 46.9%;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid var(--deep_purple);
    border-radius: 5px;
    font-weight: bold;
    letter-spacing: 1px;
    transition: all .3s ease;
    color: black;
}

.about-link:hover {
    letter-spacing: 3px;
    cursor: pointer;
}

.maxar {
    background-color: #f5b942;
}

.lspace {
    background-color: #5d26eb;
}

.ntxsd {
    background-color: #eff22c;
}

.tech4good, .ntxsc, .wave {
    background-position: center;
    background-size: contain;
    background-repeat: space;
}

@media only screen and (max-width: 880px) {
    .cluster {
        flex-direction: column;
        flex-wrap: nowrap;
        border: none;
        min-height: 150vh;
        background-color: white;
    }

    .about-desc, .about-header, .about-link, .about-title, .skills {
        width: 80vw;
    }

    .about-desc {
        height: 260px;
    }
}
.projects {
    background-color: var(--deep_purple);
    color: var(--white);
    min-height: 100vh;
    width: 100vw;
    max-width: 100%;
    text-align: center;
}

.sub-container {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    align-items: center;
}

.projects-title {
    font-size: 3em;
    text-align: center;
    padding-top: 100px;
}

.sub-title {
    display: flex;
    justify-content: center;
    margin: 20px 0px;
}

.sub-title > button {
    color: white;
    font-size: 16px;
    background-color: var(--deep_purple);
    width: 300px;
    padding: 5px;
    border: 2px solid white;
    border-radius: 5px;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    margin-bottom: 10px;
    transition: all 0.3s ease;
}

.sub-title > button > div {
    width: 200px;
}

.sub-title > button:hover {
    background-color: var(--light_purple);
    cursor: pointer;
}

.hide {
    display: none;
}

.center {
    min-height: 70vh;
    display: flex;
    justify-content: center;
    flex-direction: column;
}
.space {
    margin: 20px 50px;
}

.demo_component {
    height: 400px;
    width: 300px;
    transition: all .3s ease;
}

.demo_component:hover {
    height: 410px;
    width: 305px;
}

.thumbnail {
    height: 40%;
    width: 100%;
    background-color: white;
}

.thumbimg {
    height: 100%;
    width: 100%;
    object-fit: cover;
    border-radius: 5px;   
}

.title {
    padding: 5px;
    font-weight: bold;
    font-size: 1.5em;
}

.uses {
    font-style: italic;
    text-align: center;
    padding: 5px;
}

.desc {
    padding: 5px;
}

.demo-link {
    color: white;
}
.nav {
    background:
    linear-gradient(
        rgba(0, 1, 43, 1.0), 
        rgba(0, 1, 43, 0.0)
        );
    color: white;
    height: 74px;

    position: fixed;
    width: 100vw;
    z-index: 999;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.nav-link {
    color: white;
    font-weight: bold;
    letter-spacing: 1px;
}

.nav-link:hover {
    text-decoration: underline;
}
